import './AdminPuntoRetiroUpdate.css'

import { Geolocation } from '@capacitor/geolocation'
import { IonSpinner } from '@ionic/react'
import Leaflet, { LatLngTuple } from 'leaflet'
import { observer } from 'mobx-react-lite'
import React, { useEffect, useRef, useState } from 'react'

import { AdminItemField, Page, SelectorModal, SMap } from '../../components'
import MapaSelectorModal from '../../components/MapaSelectorModal'
import { string2leaflet_location } from '../../helpers'
import { autocompletar_calles, DetailViewProps, setearSoloCalle, useApiToaster,  useToaster, useTryGoBack } from '../../helpers-ts'
import { useStores } from '../../models/root-store/root-store-context'
import { RUTAS } from '../../constants'

Leaflet.Icon.Default.imagePath = 'images/'

const defaultValues = {
  id: 0,
  fabricante: 0,
  nombre: '',
  calle_principal: '' as any,
  calle_secundaria: '' as any,
  numero: '',
  referencia: '',
  ubicacion: '',
  default: false,
  ciudad: '',
}

export const AdminPuntoRetiroUpdate: React.FC<DetailViewProps> = observer(({ history, match }) => {
  const rootStore = useStores()
  const { adminStore } = rootStore
  const [Loading, setLoading] = useState(false)
  const [Errors, setErrors] = useState(defaultValues)
  const [Datos, setDatos] = useState(defaultValues)
  const [Ready, setReady] = useState(false)
  const Estado = history.location.pathname.split('/')[3]

  // Ubicación
  const MapaRef = useRef() // Para el modal
  const [Center, setCenter] = useState({ lng: -57.63591, lat: -25.30066 } as any)
  const [Lat, setLat] = useState<number | null>(null)
  const [Lng, setLng] = useState<number | null>(null)
  const [UbicacionUser, setUbicacionUser] = useState<LatLngTuple | null>(null)

  const apiToaster = useApiToaster()
  const addToast = useToaster()
  const tryGoBack = useTryGoBack()
  const editando = !!match.params.id

  const setup = async () => {
    obtenerCiudades()
    if (match.params.id) {
      obtenerPuntoRetiro(match.params.id)
    } else {
      const datos = { ...defaultValues }
      datos.fabricante = match.params.fabricante
      setDatos(datos)
    }
    // Timeout para renderizar correctamente el mapa
    setTimeout(() => {
      setReady(true)
    }, 700)
  }

  useEffect(() => {
    setup()
  }, [])

  useEffect(() => {
    if (rootStore.hasLocationPermission && !editando) {
      if (!Ready) {
        setTimeout(
          () =>
            actualizarUbicacionUser().catch((error) => {
              console.log(error)
              if (String(error).includes('location disabled')) {
                addToast('La ubicación está desactivada.')
              }
            }),
          700,
        )
      } else {
        actualizarUbicacionUser().catch((error) => {
          console.log(error)
          if (String(error).includes('location disabled')) {
            addToast('La ubicación está desactivada.')
          }
        })
      }
    }
  }, [rootStore.hasLocationPermission])

  const actualizarUbicacionUser = async () => {
    const res = await Geolocation.getCurrentPosition({
      enableHighAccuracy: true,
      timeout: 15000,
      maximumAge: 60 * 60 * 1000,
    })
    setUbicacionUser([res.coords.latitude, res.coords.longitude])
    setLat(res.coords.latitude)
    setLng(res.coords.longitude)
    setCenter({ lat: res.coords.latitude, lng: res.coords.longitude })
  }

  const obtenerCiudades = () => {
    if (!rootStore.ciudades.length) {
      setLoading(true)
      rootStore.actualizarDatosInicio(false).then(() => {
        setLoading(false)
      })
    }
  }

  const obtenerPuntoRetiro = (id: any) => {
    rootStore.environment.api.adminGetPuntoRetiro(id).then((result) => {
      if (result.kind === 'ok') {
        if (result.punto_retiro.ubicacion) {
          const { lat, lng } = string2leaflet_location(result.punto_retiro.ubicacion)
          setLat(lat)
          setLng(lng)
          setCenter({ lat, lng })
        }
        const datos = { ...result.punto_retiro } as any
        datos.calle_principal = { label: datos.calle_principal, value: datos.calle_principal }
        datos.calle_secundaria = { label: datos.calle_secundaria, value: datos.calle_secundaria }
        setDatos(datos)
        adminStore.setPuntoRetiroDetalle(result.punto_retiro)
      } else {
        apiToaster(result, '', 'obtener el punto de retiro')
      }
    })
  }

  const guardar = () => {
    setLoading(true)
    setErrors(defaultValues)
    const ubicacion = Lat + ',' + Lng
    const calle_principal = Datos.calle_principal
    const calle_secundaria = Datos.calle_secundaria
    const datos = {
      ...Datos,
      ubicacion: ubicacion,
      calle_principal: calle_principal ? calle_principal.value : '',
      calle_secundaria: calle_secundaria ? calle_secundaria.value : '',
    }
    rootStore.environment.api.guardarPuntoRetiro(datos).then((result: any) => {
      setLoading(false)
      apiToaster(result, 'Se guardó el punto de retiro con exito', 'guardar el punto de retiro')
      if (result.kind === 'ok') {
        setDatos(defaultValues)
        tryGoBack(RUTAS.ADMIN_FABRICANTE_DETAIL('moderado', match.params.fabricante))
      } else if (result.kind === 'bad-data') {
        setErrors(result.errors)
      }
    })
  }

  const setDato = (campo: string, valor: any) => {
    let datos = { ...Datos }
    // @ts-ignore
    datos[campo] = valor
    setDatos(datos)
  }

  const seleccionarUbicacion = (lat: any, lng: any) => {
    setLat(lat)
    setLng(lng)
    setCenter([lat, lng])
  }

  useEffect(() => {
    if ((Ready || !editando) && Lat && Lng) {
      rootStore.environment.api.infoCalle(Lat, Lng).then((result: any) => {
        if (result.kind === 'ok') {
          if (result.response.info) {
            const i = result.response.info
            const dato: any = { ...Datos }
            if (i.calle) {
              dato.calle_principal = { value: i.calle, label: i.calle }
            }
            if (i.ciudad_obj) {
              dato.ciudad = rootStore.ciudades.find((c) => c.id === i.ciudad_obj.id)
            }
            if (i.secondary) {
              dato.calle_secundaria = { value: i.secondary, label: i.secondary }
            }
            setDatos(dato)
          }
        } else {
          console.log('error infoCalle', result)
        }
      })
    }
  }, [Lat, Lng])

  const dataSelectorModal = (data: any) => {
    setDato('ciudad', data[0])
  }

  return (
    <Page login_required admin>
      <MapaSelectorModal ref={MapaRef} lat={Lat} lng={Lng} onAccept={seleccionarUbicacion} />
      <div className="page-wrapper" style={{ display: 'flex', height: '100vh', overflowY: 'scroll' }}>
        <div className="container-fluid background-white">
          <div className="row page-titles">
            <div className="col-md-5 col-12 align-self-center">
              <h3 className="text-themecolor">{Datos.id ? 'Editar punto de retiro' : 'Crear punto de retiro'}</h3>
            </div>
            <div className="col-md-7 col-12 align-self-center d-none d-md-block">
              <ol className="breadcrumb mb-0 p-0 bg-transparent float-right">
                <li className="breadcrumb-item">
                  <a onClick={() => history.push('/admin/')}>Inicio</a>
                </li>
                <li className="breadcrumb-item">
                  <a onClick={() => history.push('/admin/fabricante/' + Estado)}>Proveedores</a>
                </li>
                <li className="breadcrumb-item">
                  <a onClick={() => history.push('/admin/fabricante/' + Estado + '/detail/' + match.params.fabricante)}>
                    {adminStore.fabricanteDetalle ? adminStore.fabricanteDetalle.nombre : ''}
                  </a>
                </li>
                <li className="breadcrumb-item active">{Datos.id ? 'Editar' : 'Crear'}</li>
              </ol>
            </div>
          </div>
          <div className="card mb-0">
            <div className="card-body">
              <div className={'form-group'}>
                <label id={'nombre'}>Nombre</label>
                <input
                  type={'text'}
                  className={'form-control'}
                  id={'nombre'}
                  value={Datos.nombre}
                  onChange={(evt) => setDato('nombre', evt.target.value)}
                />
                {Errors.nombre && <div className={'text-danger'}>{Errors.nombre}</div>}
              </div>
              <div className={'form-group'}>
                <label id={'mapa'}>Seleccione la ubicación</label>
                <div style={{ height: 300, width: '100%' }}>
                  {Ready && (
                    <SMap
                      lat={Lat}
                      lng={Lng}
                      zoom={Lat ? 16 : 14}
                      center={Center}
                      bloquear_interaccion
                      onClick={() => {
                        setTimeout(() => {
                          ;(MapaRef.current as any).open()
                        }, 300)
                      }}
                    />
                  )}
                </div>
              </div>
              <div className={'row'}>
                <AdminItemField
                  deshabilitado={!Loading}
                  titulo={'Calle principal'}
                  tipo={'select2async'}
                  campo={'calle_principal'}
                  valor={Datos ? Datos.calle_principal : ''}
                  error={Errors && Errors.calle_principal}
                  set_dato={(campo: string, data: any) => {
                    if (!data) {
                      return
                    }
                    setDato(campo, data)
                  }}
                  select_placeholder={'Calle principal'}
                  select_onchange={(valor: any) => {
                    setearSoloCalle(rootStore, setDato, setDatos, Datos, 'calle_principal', valor)
                  }}
                  opciones_select={(text: string) => autocompletar_calles(rootStore, UbicacionUser, text)}
                  select_no_options_text={'No se encuentran sugerencias'}
                  columnas={12}
                  columnas_sm={12}
                />
              </div>
              <div className={'row'}>
                <AdminItemField
                  deshabilitado={!Loading}
                  titulo={'Calle secundaria'}
                  tipo={'select2async'}
                  campo={'calle_secundaria'}
                  valor={Datos ? Datos.calle_secundaria : ''}
                  error={Errors && Errors.calle_secundaria}
                  set_dato={(campo: string, data: any) => {
                    if (!data) {
                      return
                    }
                    setDato(campo, data)
                  }}
                  select_placeholder={'Calle secundaria'}
                  select_onchange={(valor: any) => {
                    setearSoloCalle(rootStore, setDato, setDatos, Datos, 'calle_secundaria', valor)
                  }}
                  opciones_select={(text: string) => autocompletar_calles(rootStore, UbicacionUser, text)}
                  select_no_options_text={'No se encuentran sugerencias'}
                  columnas={12}
                  columnas_sm={12}
                />
              </div>
              <div className={'form-group'}>
                <label id={'numero'}>Número</label>
                <input
                  type={'text'}
                  className={'form-control'}
                  id={'numero'}
                  value={Datos.numero}
                  onChange={(evt) => setDato('numero', evt.target.value)}
                />
                {Errors.numero && <div className={'text-danger'}>{Errors.numero}</div>}
              </div>
              <div className={'form-group'}>
                <label id={'referencia'}>Referencia</label>
                <input
                  type={'text'}
                  className={'form-control'}
                  id={'referencia'}
                  value={Datos.referencia}
                  onChange={(evt) => setDato('referencia', evt.target.value)}
                />
                {Errors.referencia && <div className={'text-danger'}>{Errors.referencia}</div>}
              </div>
              <SelectorModal
                titulo={'Ciudad'}
                titulo_label={'Seleccioná la ciudad'}
                listado_elementos={rootStore.ciudades}
                // @ts-ignore
                listado_elementos_seleccionados={
                  !Datos['ciudad'] && adminStore.puntoRetiroDetalle
                    ? adminStore.puntoRetiroDetalle.ciudad
                    : [Datos['ciudad']]
                }
                datos_filtrados={dataSelectorModal}
                multiple={false}
                llave_label_opcion={'nombre'}
              />
              {Errors.ciudad && <div className={'text-danger'}>{Errors.ciudad}</div>}
              <div className={'form-group'}>
                <input
                  type={'checkbox'}
                  id={'aprobado'}
                  checked={Datos.default}
                  onClick={(evt) => {
                    // @ts-ignore
                    setDato('default', !Datos.default)
                  }}
                />
                <label
                  id={'aprobado'}
                  onClick={(evt) => {
                    // @ts-ignore
                    setDato('default', !Datos.default)
                  }}
                >
                  Punto de retiro por defecto
                </label>
              </div>

              <div className={'form-group text-right mt-3'}>
                <a
                  onClick={() => tryGoBack('/admin/fabricante/' + Estado + '/detail/' + match.params.fabricante)}
                  className={'btn btn-light'}
                >
                  Cancelar
                </a>
                <button className={'btn btn-primary ml-2'} disabled={Loading} onClick={guardar}>
                  {Loading && <IonSpinner name="crescent" />}
                  {!Loading && <span>Guardar</span>}
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </Page>
  )
})

export default AdminPuntoRetiroUpdate
